@import url("//hello.myfonts.net/count/2f0a3a");
@font-face {
  font-family: 'Campton-Bold';
  src: url("../webfonts/2F0A3A_0_0.eot");
  src: url("../webfonts/2F0A3A_0_0.eot?#iefix") format('embedded-opentype'), url("../webfonts/2F0A3A_0_0.woff2") format('woff2'), url("../webfonts/2F0A3A_0_0.woff") format('woff'), url("../webfonts/2F0A3A_0_0.ttf") format('truetype');
}
@-ms-viewport {
  width: device-width;
}
html,
body {
  height: 100%;
  cursor: default;
}
html,
body,
p,
h1,
h2,
h3,
pre,
ul,
li {
  padding: 0;
  margin: 0;
}
body {
  background: #f2da9a;
  color: #000;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  font-weight: 300;
}
.content {
  max-width: 640px;
  margin: 0 5%;
  text-align: left;
}
p,
h1,
h2,
h3,
pre,
ul {
  line-height: 1.5;
}
p,
h2,
h3 {
  margin-top: 10px;
  margin-bottom: 20px;
  line-height: 1.75;
}
ul {
  padding-left: 2em;
  margin-top: -1em;
  margin-bottom: 1em;
}
a {
  color: #da4027;
  text-decoration: none;
  border-bottom: solid 1px rgba(218,64,39,0.5);
  transition: all ease 0.123s;
}
a:hover,
a:active {
  border-bottom-color: rgba(218,64,39,0.2);
}
h1,
h2,
h3 {
  font-family: Campton-Bold;
  font-weight: normal;
}
h1 {
  font-size: 2.5em;
  line-height: 1.1;
}
.maintitle {
  font-size: 1.3em;
  font-family: Campton-Bold;
  line-height: 1.3;
  margin-bottom: 0.6em;
}
h2 {
  font-size: 1.8em;
  text-transform: uppercase;
  color: #da4027;
  margin-top: 4em;
}
h3 {
  font-size: 1.4em;
  color: #000;
  margin: 1.5em 0 0;
}
.header {
  color: #da4027;
  max-width: 640px;
  margin: 0 5%;
  padding: 50px 0 20px;
}
.navigation {
  margin-bottom: 3em;
  font-family: Campton-Bold;
  text-transform: uppercase;
  line-height: 2.5;
}
.navigation a {
  margin-right: 1.5em;
}
.navigation a:after {
  content: " ↓";
}
.part {
  margin-bottom: 40px;
}
.footer {
  max-width: 640px;
  margin: 0 5%;
  padding-bottom: 60px;
}
.footer p {
  line-height: 1.2;
  font-size: 14px;
}
@media only screen and (min-width: 640px) {
  body {
    font-size: 20px;
  }
  .part {
    margin-bottom: 80px;
  }
  .maintitle {
    font-size: 2em;
  }
  .header,
  .content,
  .footer {
    margin: 0 10%;
  }
}
